import React from 'react';

function ApplicationsBlock({ applications }) {
  return (
    <div className="applications-block">
      <h3>Приложения</h3>
      <ul>
        {applications.map(app => (
          <li key={app.id}>{app.name}</li>
        ))}
      </ul>
    </div>
  );
}

export default ApplicationsBlock;
