import React from 'react';

function NodesBlock({ nodes, onSelectNode }) {
  return (
    <div className="nodes-block">
      <h3>Ноды</h3>
      <ul>
        {nodes.map(node => (
          <li key={node.id} onClick={() => onSelectNode(node.id)}>
            {node.name} - {node.metric}%
          </li>
        ))}
      </ul>
    </div>
  );
}

export default NodesBlock;
