import React from 'react';

function GroupsBlock({ groups, onSelectGroup }) {
  return (
    <div className="groups-block">
      <h3>Группы</h3>
      <ul>
        {groups.map(group => (
          <li key={group.id} onClick={() => onSelectGroup(group.id)}>
            {group.name}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default GroupsBlock;

