import React, { useState } from 'react';
import GroupsBlock from './components/GroupsBlock';
import NodesBlock from './components/NodesBlock';
import MetricsBlock from './components/MetricsBlock';
import ApplicationsBlock from './components/ApplicationsBlock';
import GeneralInfoBlock from './components/GeneralInfoBlock';
import InterfaceBlock from './components/InterfaceBlock'; // Блок интерфейсов

function App() {
  // Состояния для управления выбранной группой и нодой
  const [selectedGroup, setSelectedGroup] = useState(null);
  const [selectedNode, setSelectedNode] = useState(null);

  // Заглушки данных (для демонстрации)
  const groups = [
    { id: 1, name: 'Группа 1', nodes: [101, 103] },
    { id: 2, name: 'Группа 2', nodes: [102] },
    { id: 3, name: 'Группа 3', nodes: [104] },
  ];

  const nodes = [
    { id: 101, name: 'Нода 1', groupId: 1, metric: 88 },
    { id: 102, name: 'Нода 2', groupId: 2, metric: 93 },
    { id: 103, name: 'Нода 3', groupId: 1, metric: 78 },
    { id: 104, name: 'Нода 4', groupId: 3, metric: 96 },
  ];

  const metrics = [
    { id: 1, nodeId: 101, timestamp: '2025-04-04T14:30', value: 88 },
    { id: 2, nodeId: 102, timestamp: '2025-04-04T14:31', value: 93 },
    { id: 3, nodeId: 103, timestamp: '2025-04-04T14:32', value: 78 },
    { id: 4, nodeId: 104, timestamp: '2025-04-04T14:33', value: 96 },
  ];

  const applications = [
    { id: 1, name: 'Приложение 1', nodeId: 101 },
    { id: 2, name: 'Приложение 2', nodeId: 102 },
    { id: 3, name: 'Приложение 3', nodeId: 103 },
    { id: 4, name: 'Приложение 4', nodeId: 104 },
  ];

  const interfaces = [
    { id: 1, name: 'Интерфейс 1', nodeId: 101, status: 'Active', statusColor: 'green' },
    { id: 2, name: 'Интерфейс 2', nodeId: 102, status: 'Warning', statusColor: 'yellow' },
    { id: 3, name: 'Интерфейс 3', nodeId: 103, status: 'Critical', statusColor: 'red' },
  ];

  // Обработчики для выбора группы и ноды
  const handleGroupSelect = groupId => {
    setSelectedGroup(groupId);
    setSelectedNode(null); // Сброс выбранной ноды при выборе группы
  };

  const handleNodeSelect = nodeId => {
    setSelectedNode(nodeId);
  };

  // Фильтрация данных
  const filteredNodes = selectedGroup
    ? nodes.filter(node => node.groupId === selectedGroup)
    : nodes;

  const filteredMetrics = selectedNode
    ? metrics.filter(metric => metric.nodeId === selectedNode)
    : metrics;

  const filteredApplications = selectedGroup
    ? applications.filter(app => filteredNodes.some(node => node.id === app.nodeId))
    : applications;

  const filteredGroups = selectedNode
    ? groups.filter(group => group.nodes.includes(selectedNode))
    : groups;

  const filteredInterfaces = selectedNode
    ? interfaces.filter(iface => iface.nodeId === selectedNode)
    : interfaces;

  // Возвращаем JSX
  return (
    <div className="container">
      <div className="column">
        <GeneralInfoBlock groups={groups} nodes={filteredNodes} applications={filteredApplications} />
        <GroupsBlock groups={filteredGroups} onSelectGroup={handleGroupSelect} />
      </div>
      <div className="column">
        <NodesBlock nodes={filteredNodes} onSelectNode={handleNodeSelect} />
        <InterfaceBlock interfaces={filteredInterfaces} />
      </div>
      <div className="column">
        <MetricsBlock metrics={filteredMetrics} />
        <ApplicationsBlock applications={filteredApplications} />
      </div>
    </div>
  );
}

export default App;
