import React from 'react';

function GeneralInfoBlock({ groups, nodes, applications }) {
  const activeNodes = nodes.filter(node => node.metric <= 85).length;
  const warningNodes = nodes.filter(node => node.metric > 85 && node.metric <= 95).length;
  const criticalNodes = nodes.filter(node => node.metric > 95).length;

  return (
    <div className="general-info-block">
      <h3>Общая информация</h3>
      <p>Групп: {groups.length}</p>
      <p>Нод: {nodes.length}</p>
      <p>Активных нод: {activeNodes}</p>
      <p>Предупреждений: {warningNodes}</p>
      <p>Критических: {criticalNodes}</p>
      <p>Приложений: {applications.length}</p>
    </div>
  );
}

export default GeneralInfoBlock;
