import React from 'react';

function InterfaceBlock({ interfaces }) {
  return (
    <div className="interface-block">
      <h3>Интерфейсы</h3>
      <ul>
        {interfaces.map(iface => (
          <li key={iface.id} style={{ backgroundColor: iface.statusColor }}>
            {iface.name} - {iface.status}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default InterfaceBlock;

